import React, { useEffect } from 'react';
import { Link } from "react-router-dom";
import { useHistory } from 'react-router-dom'

import './UserOrders.scss'
//cmps
import { List } from '../../cmps/List/List'

export default function UserOrders(props) {
    const { user } = props

    const history = useHistory();

    function goShop() {
        history.push('/shop')
    }



    return (
        <section className="flex user-orders">
            {!user.orders.length &&
                <div className="no-orders">
                    <h1>   </h1>
                    <h3>  ,   </h3>
                    <button className="app-btn" onClick={() => goShop()}></button>
                </div>}
            <div className="orders-list">
                {!!user.orders.length && user.orders.map(order => {
                    return <div className="order" key={order.id}>
                        <img src={order.items[0].imgUrls[0]} alt="" />
                        <div className="flex column">
                            <div className="order-details rtl">
                                <p> : {new Date(order.createdAt).toLocaleDateString('en-GB')}</p>
                                <p className="order-id">  <label > :</label>{order.id}</p>
                                <p> : {order.items.length}</p>
                                <p> : {order.totalPrice}</p>
                                <Link to={`/order/${order.id}`}> </Link>
                            </div>
                        </div>
                    </div>
                })}
            </div>

        </section>
    )
}
